// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import Flatpickr from 'react-flatpickr'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Form,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  Table,
  Modal,
  CardTitle,
  ModalHeader,
  Badge,
  ModalBody,
  Progress
} from 'reactstrap'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import axios from '@src/configs/axios/axiosConfig'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components
import classnames from 'classnames'
import moment from 'moment'
import toast from 'react-hot-toast'
import { Edit, Trash2, Plus, X } from 'react-feather'
import { useDropzone } from 'react-dropzone'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

const Licenses = ({ }) => {

  const MySwal = withReactContent(Swal)
  const [data, setData] = useState([])

  const id = localStorage.getItem('clientId')
  const statusArr = ['', 'Active', '', 'Expired']
  const statusColArr = ['', 'light-primary', '', 'light-danger']
  const [formModal, setFormModal] = useState(false)
  const [selected, setSelected] = useState(null)
  const [filesArr, setFilesArr] = useState([])
  const [attachmentIds, setAttachmentsId] = useState([])
  const [fileUploadProgress, setProgress] = useState(0)
  const fileClassNames = {
    image: "ui-attachment--image",
    pdf: "ui-attachment--pdf",
    xlsx: "ui-attachment--excel",
    xls: "ui-attachment--excel",
    docx: "ui-attachment--docx",
    doc: "ui-attachment--docx"
  }
  const schema = yup.object().shape({
    name: yup.string().required("Please Enter Name"),
    id: yup.string(),
    contactid: yup.string().default(id),
    createdby: yup.string().default(userId),
    updatedby: yup.string().default(userId),
    organizationid: yup.string().default(activeOrgId),
    licenseno: yup.string().required('Please Enter License Number'),
    issueddate: yup.string().default('').required("Please Enter Issued Date"),
    expirydate: yup.string().default('')
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const resetForm = () => {
    setValue('name', '')
    setValue('issueddate', '')
    setValue('expirydate', '')
    setValue('licenseno', '')
    setValue('contactid', id)
    setValue('id', '')
    setValue('attachmentIds', [])
    setValue('organizationid', activeOrgId)
    setValue('createdby', userId)
    setValue('updatedby', userId)
  }

  const closeModal = () => {
    setFormModal(false)
    resetForm()
  }

  const openModal = () => {
    setFormModal(true)
    resetForm()
  }

  const openEditModal = card => {
    setSelected(card)
    setFormModal(true)
    const issueddate = moment(card.issueddate, 'x').format('MMM DD, Y')
    const expirydate = moment(card.expirydate, 'x').format('MMM DD, Y')
    setValue('name', card.name)
    setValue('issueddate', issueddate)
    setValue('expirydate', expirydate)
    setValue('licenseno', card.licenseno)
    setValue('contactid', id)
    setValue('id', card.id)
    setValue('organizationid', activeOrgId)
    setValue('createdby', userId)
    setValue('updatedby', userId)
    setValue('attachmentIds', card.attachmentids ?? [])
    setAttachmentsId(card.attachmentids)

  }

  const getLicenses = () => {
    axios.post('/digitalsignatures/listbyclient', { contactid: id, licenseType: false })
      .then((res) => { setData(res.data.digitalsignatures) })
      .catch((err) => { console.log(err) })
  }

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      //Set the progress value to show the progress bar
      setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const uploadFiles = (files) => {

    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })

    formData.append('organizationId', activeOrgId)
    formData.append('clientId', id)

    const folderPath = [localStorage.getItem('clientName'), 'Licence']
    formData.append('folderPath', folderPath)
    setBlock(true)

    axios.post('/files/upload', formData, configUpload).then((res) => {
      setAttachmentsId([...attachmentIds, ...res.data.attachment_ids])
      setBlock(false)
    }).catch((err) => { console.log(err); setBlock(false) })
  }

  const { getRootProps, getInputProps } = useDropzone({
    multiple: true,
    onDrop: async (files, rejectedFiles) => {

      const acceptedFiles = []
      files.forEach(obj => {
        obj.extension = obj.name.split('.').pop()
        acceptedFiles.push(obj)
      })
      if (rejectedFiles.length) {
        toast.error('You can only upload image Files!.')
      } else {
        await uploadFiles(acceptedFiles)
        setFilesArr([...filesArr, ...acceptedFiles])
      }
    }
  })

  const handleRemoveFile = (file, index) => {
    const uploadedFiles = filesArr
    const filtered = uploadedFiles.filter(i => i.name !== file.name)
    setAttachmentsId([...attachmentIds.splice(index, 1)])
    setFilesArr(filtered)
  }

  const fileList = filesArr.map((file, index) => (
    <Col md={6} key={`${file.name}-${index}`} className={`d-flex align-items-center justify-content-between ui-attachment ${fileClassNames[file.extension] || fileClassNames['image']}`}>
      <div className='file-details d-flex align-items-center'>
        <div>
          <p className='file-name mb-0 text-overflow-ellipsis'>{file.name.split('.')[0].length > 15 ? `${file.name.split('.')[0].substr(0, 15)}` : file.name}</p>
        </div>
      </div>
      <X size={14} className='cursor-pointer' onClick={() => handleRemoveFile(file, index)} />
    </Col>
  ))

  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await axios.post('/digitalsignatures/delete', { id, updatedBy: userId })
        getLicenses()
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'License has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const onSubmit = async data => {
    data.issueddate = moment(data.issueddate).valueOf()
    data.expirydate = data.expirydate !== '' ? moment(data.expirydate).valueOf() : 0
    data.attachmentIds = attachmentIds
    const today = moment().startOf('d').valueOf()
    data.status = today > data.expirydate ? 3 : 1
    if (selected !== null) {
      axios.post('/digitalsignatures/updatelicenses', data)
        .then(() => {
          getLicenses()
          setFormModal(false)
          toast.success('License Updated Successfully', { position: 'bottom-right' })
          setFilesArr([])
          setProgress(0)
          setAttachmentsId([])
        })
        .catch((err) => { console.log(err) })
      setSelected(null)
    } else {

      axios.post('/digitalsignatures/addlicenses', data)
        .then(async () => {
          getLicenses()
          setFormModal(false)
          setFilesArr([])
          setAttachmentsId([])
          toast.success('License Added Successfully', { position: 'bottom-right' })
        })
        .catch((err) => { console.log(err) })
    }

    resetForm()
  }

  const dateFormat = (value) => {
    if (value === null || value === undefined) {
      return ''
    }
    if (value.length === 13) {
      return moment(value, 'x').format('MMM DD, YYYY')
    } else {
      return moment.unix(value).format('MMM DD, YYYY')
    }
  }

  useEffect(() => {
    getLicenses()
  }, [])

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const handleRemoveFileFromLicense = (id) => {

    const tempIds = [...attachmentIds]
    tempIds.splice(tempIds.indexOf(id), 1)
    setAttachmentsId(tempIds)
    const tempfile = [...selected.files]
    const temp = tempfile.filter((ob) => { return ob.id !== id })

    const test = { ...selected }
    test.files = temp
    setSelected(test)
    setValue('attachmentIds', temp.map((obj) => obj.id) ?? [])

  }

  return (
    <Card>
      <CardBody >
        <CardTitle className='align-items-end card-title d-flex justify-content-between mb-0' tag='h5'>
          Licenses
          <div className='d-flex'>
            <Button size='sm' className='me-1' color='primary' onClick={() => { openModal() }}>
              <Plus size={14} />
              Add New
            </Button>
          </div>
        </CardTitle>
      </CardBody>
      <Table className='text-nowrap text-center border-bottom' responsive>
        <thead>
          <tr>
            <th className='text-start'>Name</th>
            <th>Number</th>
            <th>Start Date</th>
            <th>Expiry Date</th>
            <th>Files</th>
            <th>Status</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {data.map((item, index) => {
            return (
              <tr key={index}>
                <td className='text-start'>{item.name}</td>
                <td>
                  {item.licenseno}
                </td>
                <td>
                  {item.issueddate !== undefined ? dateFormat(item.issueddate) : ''}
                </td>
                <td>
                  {item.expirydate > 0 ? dateFormat(item.expirydate) : ''}
                </td>
                <td className='text-start'>
                  {
                    item.files?.map((file, k) => {
                      return (
                        <Badge key={k} className='ms-25' color='primary' >
                          <a target='_blank' className='file-name text-overflow-ellipsis' href={`http://docs.google.com/fileview?id=${file.driveid}&hl=en&pid=explorer&efh=false&a=v&chrome=false&embedded=true`}>{file.originalfilename.split('.')[0].length > 1 ? `${file.originalfilename.split('.')[0].substr(0, 10)}.${file.extension}` : file.originalfilename}</a>
                        </Badge>
                      )
                    })
                  }
                </td>
                <td>
                  <Badge color={statusColArr[item.status]}> {statusArr[item.status]}</Badge>
                </td>
                <td>
                  <div className='column-action d-flex align-items-center'>
                    <Col
                      onClick={() => { openEditModal(item) }}>
                      <Edit className='cursor-pointer mt-0' size={16} />
                    </Col>
                    <Col lg={4}
                      onClick={() => { deletefn(item.id) }}>
                      <Trash2
                        className='cursor-pointer ms-1 mt-0' size={16} />
                    </Col>
                  </div>
                </td>
              </tr>
            )
          })}
        </tbody>
      </Table>
      <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
        <ModalHeader toggle={() => closeModal()}>Licenses</ModalHeader>
        <ModalBody>
          <UILoader blocking={block} loader={<Loader />}>
            {
              Object.keys(errors).map((key) => {

                return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

              })
            }
            <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

              {getRow('Name', 'name', true)}

              {getRow('License No', 'licenseno', true)}

              <Col md={12}>
                <Label className='form-label required' for='comments'>
                  Issued Date
                </Label>
                <Controller
                  control={control}
                  name={`issueddate`}
                  id='issueddate'
                  render={({ field }) => (
                    <Flatpickr
                      value={field.value}
                      onChange={(date, dateStr) => { field.onChange(dateStr) }}
                      options={{ altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
                      className='form-control invoice-edit-input date-picker'
                    />
                  )}
                />
                {errors.issueddate && <p className='text-danger' >{errors.issueddate.message}</p>}

              </Col>

              <Col md={12}>
                <Label className='form-label' for='comments'>
                  Expiry Date
                </Label>
                <Controller
                  control={control}
                  name={`expirydate`}
                  id='expirydate'
                  render={({ field }) => (
                    <Flatpickr
                      value={field.value}
                      onChange={(date, dateStr) => { field.onChange(dateStr) }}
                      options={{ altInput: true, altFormat: "M j, Y", dateFormat: "M j, Y" }}
                      className='form-control invoice-edit-input date-picker'
                    />
                  )}
                />
                {errors.expirydate && <FormFeedback>{errors.expirydate.message}</FormFeedback>}

              </Col>
              <Col>
                <input {...getInputProps()} />
                <Button color='primary' {...getRootProps({})}>Add Attachments</Button>
              </Col>
              {fileUploadProgress > 0 && <Progress color={fileUploadProgress < 100 ? 'primary' : 'success'} value={fileUploadProgress} />}
              <Row className='d-flex p-1'>{fileList}</Row>

              <Row className='d-flex col-12'>
                {selected?.files?.map((file, k) => {
                  return (<Badge key={k} color={'primary'} className='me-1 mt-1 col-4' onClick={() => handleRemoveFileFromLicense(file.id)}>
                    {file.originalfilename.split('.')[0].length > 15 ? `${file.originalfilename.split('.')[0].substr(0, 15)}` : file.originalfilename} X</Badge>)
                })
                }
              </Row>

              <Col className='mt-2 pt-1' xs={12}>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button className='me-1' color='warning' onClick={() => closeModal()}>
                  Cancel
                </Button>
              </Col>
            </Row>
          </UILoader>
        </ModalBody>
      </Modal>
    </Card>
  )
}

export default Licenses
